.History {
    display: flex;
    min-height: 100vmin;
    /* max-height: 680px; */
    background-color: white;
    justify-content: space-evenly;
    align-items: center;
    color: black;
    letter-spacing: 4px;
    flex-wrap: wrap;
    flex-direction: column;
}

.History > h1 {
    text-transform: uppercase;
    font-weight: 200;
    letter-spacing: 12px;
    flex: 0 0 100%;
    display: block;
}

.History-text {
    display: block;
    width: 80vmin;
    line-height: 2.5em;
    max-width: 750px;
}

@media (max-width: 1000px) {
    .History {
        padding: 4em 0;
    }
}