.Service {
    display: flex;
    flex-wrap: wrap;
}

.Service-tile {
    height: 100vmin;
    max-height: 680px;
    flex: 0 0 50vw;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.Service-tile > h1 {
    text-transform: uppercase;
    font-weight: 200;
    letter-spacing: 12px;
}

.Service-list {
    list-style: none;
    margin-block-start: 0;
    padding-inline-start: 0;
}

.Service-list-item {
    text-transform: uppercase;
    line-height: 2em;
    letter-spacing: 4px;
}

.Service-tile.Services {
    order: 1;
}

.Service-tile.Image {
    order: 2;
    background-image: url("/schneider.jpg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

@media (max-width: 1000px) {
    .Service-tile {
        flex: 0 0 100vw;
    }

    .Service-tile > h1 {
        letter-spacing: 8px;
    }
    .Service-tile.Services {
        order: 2;
    }
    .Service-tile.Image {
        order: 1;
    }
}