.Map {
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
}

.Map-tile {
    flex: 0 0 50vw;
    height: 100vmin;
    max-height: 800px;
}

.Map-tile.Address {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-transform: uppercase;
    letter-spacing: 4px;
    line-height: 3em;
}

.Map-tile.Address span a {
    color: black;
}

@media (max-width: 1000px) {
    .Map-tile {
        flex: 0 0 100vw;
        min-height: 100vmin;
    }
}