.Footer {
    /* height: 250px; */
    height: 150px;
    /* background-color: #CFD8DC; */
    /* background-color: rgba(159, 187, 175, 0.46); */
    background-color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.Footer-wrap {
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    flex: 0 0 100%;
    max-width: 800px;
}

.Footer-logo {
    background-image: url("/logo.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.7;
    height: 100px;
    flex: 0 0 100vw;
}

.Footer-item-link {
    text-transform: uppercase;
    color: black;
    text-decoration: none;
    letter-spacing: 4px;
    font-size: x-small;
}

@media (max-width: 1000px) {
    .Footer {
        padding: 2em 0;
    }
    .Footer-large {
        flex: 0 0 98vw;
    }
}