.Times {
    display: flex;
    height: 100vmin;
    max-height: 680px;
    /* background-color: #64b5f6; */
    /* background-color: rgba(100, 181, 246, 0.46); */
    /* background-color: rgba(159, 187, 175, 0.46); */
    /* background-color: #CFD8DC; */
    background-color: black;
    justify-content: center;
    align-items: center;
    color: white;
    letter-spacing: 4px;
    flex-wrap: wrap;
    flex-direction: column;
}

.Times > h1 {
    text-transform: uppercase;
    font-weight: 200;
    letter-spacing: 12px;
}

.Team-names {
    text-transform: uppercase;
    letter-spacing: 4px;
}

@media (max-width: 1000px) {
    .Times {
        min-height: 140vmin;
    }

    .Times > h1 {
        letter-spacing: 8px;
    }
}