.Team {
    display: flex;
    height: 100vmin;
    /* max-height: 680px; */
    background-color: black;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    letter-spacing: 4px;
    flex-wrap: wrap;
    flex-direction: column;
}

.Team > h1 {
    text-transform: uppercase;
    font-weight: 200;
    letter-spacing: 12px;
}

.Team-container {
    display: flex;
    flex-direction: column;
}

.Team-image {
    background-image: url("/wowaundlena2_sq_filt.jpg");
    background-position: center;
    width: 60vmin;
    height: 60vmin;
    max-width: 320px;
    max-height: 320px;
    border-radius: 50%;
    background-size: cover;
    background-repeat: no-repeat;
}

@media (max-width: 1000px) {
    .Team {
        min-height: 140vmin;
    }

    .Team > h1 {
        letter-spacing: 8px;
    }
}