.Menu {
    height: 150px;
    /* background-color: #CFD8DC; */
    /* background-color: rgba(159, 187, 175, 0.46); */
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.Menu-logo {
    background-image: url("/logo_white.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.7;
    max-height: 80%;
    height: 150px;
    flex: 0 0 0vw;
}

.Menu-large {
    display: flex;
    justify-content: space-evenly;
    flex: 0 0 38vw;
    align-items: center;
}

.Menu-item-link {
    text-transform: uppercase;
    color: white;
    text-decoration: none;
    letter-spacing: 4px;
    font-size: smaller;
}

.Menu-burger-wrap {
    display: none;
    flex: 0 0 100vw;
}

.Menu-burger {
    color: white;
    background-color: rgba(0,0,0,0);
    border: none;
    font-size: 5em;
}

.Menu-mobile {
    flex: 0 0 100%;
    display: none;
}

.Menu-mobile-list {
    margin-block-start: 0;
    padding-inline-start: 0;
}

.Menu-mobile-item {
    list-style: none;
    height: 3em;
}

.Menu-mobile-item-link {
    text-transform: uppercase;
    color: white;
    text-decoration: none;
    letter-spacing: 4px;
}

@media (max-width: 1000px) {
    .Menu {
        justify-content: flex-end;
    }
    .Menu.open {
        height: auto;
    }
    .Menu-large {
        display: none;
    }
    .Menu-mobile.open {
        display: block;
    }
    .Menu-burger-wrap {
        display: block;
    }
    .Menu.open .Menu-burger-wrap {
        flex: 0 0 50vw;
    }
    .Menu.open .Menu-logo {
        flex: 0 0 37vw;
    }
}