.App {
  text-align: center;
}

.App-logo {
  height: 25vmin;
  pointer-events: none;
  z-index: 2;
  opacity: 0.7;
}

.App-header {
  background-color: #282c34;
  background-image: url("/measure-tape-1425180_blur.jpg");
  /* background-attachment: fixed; */
  background-position: center;
  min-height: calc(100vh - 150px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-thread {
  background-image: url("/assorted-color-threads-in-spools-1189675.jpg");
  height: 100vmin;
  max-height: 680px;
  background-position: center;
  background-size: cover;
}

